ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

ul li {
	cursor: pointer;
	position: relative;
	padding: 12px 8px 12px 40px;
	background: #eee;
	font-size: 18px;
	transition: 0.2s;
}

ul li:nth-child(odd) {
	background: #f9f9f9;
}

ul li:hover, ul li:focus {
	background: #ddd;
}

ul li.checked {
	background: #888;
	color: #fff;
	text-decoration: line-through;
}

ul li.checked::before {
	content: '';
	position: absolute;
	border-color: #fff;
	border-style: solid;
	border-width: 0 2px 2px 0;
	top: 10px;
	left: 16px;
	transform: rotate(45deg);
	height: 15px;
	width: 7px;
}

.close {
	position: absolute;
	right: 0;
	top: 0;
	padding: 12px 16px 12px 16px;
}

.close:hover {
	background-color: #f44336;
	color: white;
}
